___CSS_LOADER_EXPORT___.push([module.id, `@charset "UTF-8";

:root {
    --blue: #054f77;
    --off-white: #f5f5f5;
    --orange: #ff9e3d;
    --default-font: 'Roboto', sans-serif;
    --title-font: 'Abril Fatface', serif;
}

* {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}

html, body {
    background: var(--blue);
    font-family: var(--default-font);
    font-size: 1.2em;
    line-height: 1.5em;
}

main section {
    background: var(--off-white);
    margin: 30px auto;
    padding: 20px;
    max-width: 680px;
    border-radius: 10px;
}
main h1 {
    font-family: var(--title-font);
    font-weight: 400;
    text-align: center;
}

main p.password-display {
    margin: 40px;
    text-align: center;
    font-size: 1.8em;
    color: var(--blue);
}

main p.validation-display {
    font-size: 0.8em;
    color: #f00;
}

form div {
    margin: 10px;
}

form #noc-id {
    width: 80px;
    height: 30px;
    padding: 10px;
    font-size: 1em;
    text-align: left;
    border: none;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.600);
    border-radius: 5px;
}

form #noc-id:focus {
    outline: none;
}

form .input-checkbox {
    appearance: none;
    position: relative;
    height: 20px;
    width: 20px;
    border: 1px solid;
    cursor: pointer;
    background: #fff;
}

form .input-checkbox:checked {
    background: var(--off-white);
    border: none;
}

form .input-checkbox:checked::after {
    content: "L";
    font-size: 1.5em;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg) scaleX(-1);
}

form button {
    border: none;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.600);
    background: var(--orange);
    height: 50px;
    width: 200px;
    border-radius: 15px;
    font-size: 1em;
    margin-top: 10px;
    margin-bottom: 10px;
    cursor: pointer;
}

form button:hover {
    background: #ff8000;
}

footer {
    position: absolute;
    bottom: 0;
    width: 100%;
}

footer > p {
    background: var(--off-white);
    font-size: 0.8em;
    text-align: center;
}

footer a {
    text-decoration: none;
    color: var(--blue);
}

footer a:hover {
    text-decoration: underline;
    color: var(--orange);
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA,gBAAgB;;AAIhB;IACI,eAAe;IACf,oBAAoB;IACpB,iBAAiB;IACjB,oCAAoC;IACpC,oCAAoC;AACxC;;AAEA;IACI,sBAAsB;IACtB,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,uBAAuB;IACvB,gCAAgC;IAChC,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,4BAA4B;IAC5B,iBAAiB;IACjB,aAAa;IACb,gBAAgB;IAChB,mBAAmB;AACvB;AACA;IACI,8BAA8B;IAC9B,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;IAChB,WAAW;AACf;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,aAAa;IACb,cAAc;IACd,gBAAgB;IAChB,YAAY;IACZ,4CAA4C;IAC5C,kBAAkB;AACtB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,iBAAiB;IACjB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,4BAA4B;IAC5B,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,yDAAyD;AAC7D;;AAEA;IACI,YAAY;IACZ,4CAA4C;IAC5C,yBAAyB;IACzB,YAAY;IACZ,YAAY;IACZ,mBAAmB;IACnB,cAAc;IACd,gBAAgB;IAChB,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,WAAW;AACf;;AAEA;IACI,4BAA4B;IAC5B,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,qBAAqB;IACrB,kBAAkB;AACtB;;AAEA;IACI,0BAA0B;IAC1B,oBAAoB;AACxB","sourcesContent":["@charset \"UTF-8\";\r\n\r\n@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');\r\n\r\n:root {\r\n    --blue: #054f77;\r\n    --off-white: #f5f5f5;\r\n    --orange: #ff9e3d;\r\n    --default-font: 'Roboto', sans-serif;\r\n    --title-font: 'Abril Fatface', serif;\r\n}\r\n\r\n* {\r\n    box-sizing: border-box;\r\n    margin: 0px;\r\n    padding: 0px;\r\n}\r\n\r\nhtml, body {\r\n    background: var(--blue);\r\n    font-family: var(--default-font);\r\n    font-size: 1.2em;\r\n    line-height: 1.5em;\r\n}\r\n\r\nmain section {\r\n    background: var(--off-white);\r\n    margin: 30px auto;\r\n    padding: 20px;\r\n    max-width: 680px;\r\n    border-radius: 10px;\r\n}\r\nmain h1 {\r\n    font-family: var(--title-font);\r\n 